<template>
  <div id="app" :class="currentRouteName">
    <Header />
    <router-view></router-view>
  </div>
</template>

<script>
import Header from "@/components/Header.vue";
import SiteImage from "@/assets/img/mikemooredev.jpg";

export default {
  name: "App",
  components: {
    Header,
  },
  data() {
    return {
      pageTitle: this.$route.meta.title || process.env.VUE_APP_TITLE,
      pageDescription:
        this.$route.meta.description || process.env.VUE_APP_DESCRIPTION,
      pageUrl: null,
      pageImage: `${process.env.VUE_APP_URL}${SiteImage}`,
      currentRouteName: this.$route.name,
      previousRouteName: "",
      previousRoutePath: "",
      previousRoute: {},
    };
  },
  beforeMount() {
    this.setMeta();
  },
  watch: {
    $route(to, from) {
      this.currentRouteName = to.name;
      this.previousRouteName = from.name;
      this.previousRoutePath = from.path;
      this.previousRoute = from;
      this.setMeta();
    },
  },
  methods: {
    setMeta() {
      this.pageUrl = `${window.location.href.replace(/\/$/, ``)}/`;
      this.updateTag(`link`, `rel`, `canonical`, `href`, this.pageUrl);

      this.updateTag(`title`, ``, ``, ``, this.pageTitle);
      this.updateTag(`meta`, `name`, `title`, `content`, this.pageTitle);
      this.updateTag(
        `meta`,
        `name`,
        `description`,
        `content`,
        this.pageDescription
      );

      this.updateTag(
        `meta`,
        `name`,
        `twitter:title`,
        `content`,
        this.pageTitle
      );
      this.updateTag(
        `meta`,
        `name`,
        `twitter:description`,
        `content`,
        this.pageDescription
      );
      this.updateTag(
        `meta`,
        `name`,
        `twitter:image`,
        `content`,
        this.pageImage
      );
      this.updateTag(
        `meta`,
        `name`,
        `twitter:image:alt`,
        `content`,
        process.env.VUE_APP_AUTHOR
      );

      this.updateTag(`meta`, `property`, `og:type`, `content`, `website`);
      this.updateTag(`meta`, `property`, `og:title`, `content`, this.pageTitle);
      this.updateTag(
        `meta`,
        `property`,
        `og:description`,
        `content`,
        this.pageDescription
      );
      this.updateTag(`meta`, `property`, `og:url`, `content`, this.pageUrl);
      this.updateTag(`meta`, `property`, `og:image`, `content`, this.pageImage);
    },
    updateTag(
      htmlTagType,
      selectorAttributeName,
      selectorValue,
      attributeToUpdate,
      attributeValue
    ) {
      let htmlTag = null;

      if (htmlTagType === `title`) {
        htmlTag = document.head.querySelector(`${htmlTagType}`);
      } else {
        htmlTag = document.head.querySelector(
          `${htmlTagType}[${selectorAttributeName}="${selectorValue}"]`
        );
      }

      if (typeof htmlTag === "undefined" || !htmlTag || htmlTag === null) {
        htmlTag = document.createElement(htmlTagType);
      }

      if (htmlTagType === `title`) {
        htmlTag.innerHTML = attributeValue;
      } else {
        htmlTag.setAttribute(selectorAttributeName, selectorValue);
        htmlTag.setAttribute(attributeToUpdate, attributeValue);
      }

      document.head.appendChild(htmlTag);
    },
  },
};
</script>

<style lang="scss">
@import "~@/assets/scss/app.scss";
</style>