<template>
    <div class="blog content">

        <div class="flex-wrapper mrl-0-1">

            <div class="flex-item fb-xxl-25 prl-1 order-xl-0 order-xxl-1">

                <TagCloud :tags="getTags" />

            </div>

            <div class="flex-item fb-xxl-75 prl-1 order-xl-1 order-xxl-0">

                <!--ul class="breadcrumb">
                    <li><router-link to="/">Home</router-link></li>
                    <li>Blog</li>
                </ul-->

                <section class="archive">

                    <div class="flex-wrapper mrl-0-1 mb-1 archive">

                        <article v-for="(item, index) in getArticles" 
                            :key="index"
                            class="flex-item archive__item fg-1 fb-sm-50 fb-xl-33 prl-1 mb-2"
                            :class="`flex-item-${index}`">

                            <router-link class="archive__item__link" :to="{ path: `/blog/${index}` }">                                
                                <span class="archive__item__thumbnail">
                                    <img v-if="item.thumbnail" :src="item.thumbnail" @load="imgLoaded" />
                                </span>
                                <span class="archive__item__title">{{ item.title }}</span>
                                <span class="archive__item__description h5">{{ item.description }}</span>
                                <!--span class="archive__item__date h5">{{ item.date }}</span-->
                            </router-link>

                        </article>

                    </div>

                </section>

            </div>

        </div>

    </div>
</template>

<script>
import blogArticles from '@/components/content/blog/index.json'
import TagCloud from '@/components/blog/TagCloud'

export default {
    name: 'Blog',    
    components: {
        TagCloud
    },
    data() {
        return {
            pageTitle: ``,
            pageDescription: ``,
            pageUrl: ``,
            articles: blogArticles,
            tags: {}
        }
    },
    beforeMount() {
        this.setMeta()
    },
    watch: {
        $route (){
            this.setMeta()
        }
    },
    methods: {
        imgLoaded(event) {
            event.target.classList.add("hasLoaded");
        },
        getTaggedArticles(tag) {
            
            let self = this;
            let taggedArticles = {};

            if(typeof self.tags[tag] !== "undefined"){

                let slugs = self.tags[tag];

                taggedArticles = Object.fromEntries(
                    Object.entries(self.articles)
                    .filter(([key]) => slugs.includes(key))
                );
                
            }

            return taggedArticles;
        },
        setMeta() {
            this.pageUrl = `${window.location.href.replace(/\/$/, "")}/`
            this.pageDescription = this.$route.meta.description

            let currentTag = '';
            // append blog archive tag to page title
            if(typeof this.$route.params.tag !== "undefined") {
                currentTag = `/ ${this.$route.params.tag}`
                this.pageDescription = `Articles tagged as "${this.$route.params.tag}"`
            }

            // append tag to page title (if there is one)
            this.pageTitle = `${this.$route.meta.title} ${currentTag}`  

            this.$parent.updateTag(`title`,``,``,``,this.pageTitle)
            this.$parent.updateTag(`link`,`rel`,`canonical`,`href`,this.pageUrl)
            this.$parent.updateTag(`meta`,`name`,`title`,`content`,this.pageTitle)
            this.$parent.updateTag(`meta`,`name`,`description`,`content`,this.pageDescription)
            this.$parent.updateTag(`meta`,`name`,`twitter:title`,`content`,this.pageTitle)
            this.$parent.updateTag(`meta`,`name`,`twitter:description`,`content`,this.pageTitle)
            this.$parent.updateTag(`meta`,`property`,`og:title`,`content`,this.pageTitle)
            this.$parent.updateTag(`meta`,`property`,`og:description`,`content`,this.pageDescription)
            this.$parent.updateTag(`meta`,`property`,`og:url`,`content`,this.pageUrl)
        }
    },
    computed: {
        getArticles() {

            let articles = {}

            if(typeof this.$route.params.tag === "undefined"){

                articles = this.articles;

            }else{

                articles = this.getTaggedArticles(this.$route.params.tag);

            }

            const publishedArticles = Object.fromEntries(
                Object.entries(articles)
                .filter(([key]) => {
                    return articles[key].published === true
                })
            );

            return publishedArticles

        },
        getTags() {

            let self = this;

            self.tags = {};

            Object.keys(blogArticles).forEach((key) => {

                if(blogArticles[key].published === true){
                    const tags = blogArticles[key].tags;

                    if(Array.isArray(tags)) {

                        tags.forEach((item) => {
                            if(typeof self.tags[item] === "undefined"){
                                self.tags[item] = []
                            }
                            if(typeof self.tags[item][key] === "undefined"){
                                self.tags[item].push(key);
                            }
                        });

                    }
                }

            });

            return self.tags;

        }
    }
}
</script>

<style lang="scss" scoped>
.archive {

    min-width:100%;

    .archive__item { 
        
        @include breakpoint(tablet) {
            &.fg-1 {
                flex-grow: 0;
            }
        }

        a,
        img,
        span {
            display:block;
            width:100%;
        }

        span {
            margin:0.5rem 0;
            @include transition(color);
            &:last-of-type {
                margin-bottom:0;
            }
        }

        .archive__item__thumbnail {
            background-color: color(black);
            position: relative;
            height:0;
            padding-top:65%;
            margin-top:0;
            margin-bottom:1rem;
            overflow:hidden;
            img {
                position:absolute;
                top:0;
                width:100%;
                opacity:0;
                @include transition(opacity);
                &.hasLoaded {
                    opacity:1;
                }
            }
        }

        .archive__item__description {
            color: color(grey);
        }

        .archive__item__link {
            background-color: color(grey-dark);
            border-bottom:2px solid color(black);
            color: color(white);
            padding:1rem;

            @include transition(border-color);

            &:hover,
            &:active {
                border-bottom:2px solid color(purple);
                span {
                    color: color(purple-light);                    
                }
            }

            &:focus {
                border-bottom:2px solid color(purple-light);
                span {                    
                    color: color(purple-lighter);
                }
            }

            &:hover,
            &:active,
            &:focus {
                span.archive__item__description {
                    color: color(grey);
                }
            }

        }        

    }
}
</style>