<script>
import HeaderImage from '@/assets/img/header-bg-min.png'

import ig1 from '@/assets/img/instagram/ig-1-min.jpg'
import ig2 from '@/assets/img/instagram/ig-2-min.jpg'
import ig3 from '@/assets/img/instagram/ig-3-min.jpg'
import ig4 from '@/assets/img/instagram/ig-4-min.jpg'
import ig5 from '@/assets/img/instagram/ig-5-min.jpg'
import ig6 from '@/assets/img/instagram/ig-6-min.jpg'

import Intro from '@/components/content/home/Intro.md'

export default {
    name: 'Home',
    components: {
        Intro
    },
    data() {
        return {
            header: {
                backgroundImage: HeaderImage
            },
            instagram: [
                ig1,
                ig2,
                ig3,
                ig4,
                ig5,
                ig6
            ]
        }
    },
    methods: {
        getMarkdown(markDownFiles) {
            let components = {};            
            const componentList = markDownFiles.keys().map(markDownFiles);
            
            componentList.map((x,index) => {
                let item = {};

                /*
                console.table(x.default.render);
                let filePathSlugParts = x.default.__file.replace('.md','').replace( /([a-z])([A-Z])/g, '$1-$2' ).toLowerCase().split('/');

                let fileSlug = filePathSlugParts.pop();
                let fileSlugParts = fileSlug.split('.');
                */
                
                item.component = x.default;
                
                /*
                item.cssClass = fileSlugParts.reverse().pop();
                item.index = fileSlugParts.pop();
                */

                components[index] = item;

            });

            // sort the object
            components =  Object.keys(components).sort().reduce((r, k) => (r[k] = components[k], r), {});
            return components;
        }
    },
    computed: {
        getWhatDo() {
            const markDownFiles = require.context('@/components/content/home/whatdo', false, /^.*\.(md)$/);
            return this.getMarkdown(markDownFiles);
        },
        getTestionials() {
            const markDownFiles = require.context('@/components/content/home/testimonials', false, /^.*\.(md)$/);
            return this.getMarkdown(markDownFiles);
        }
    }
}
</script>

