<template>
    <div class="contact">
        
        <section class="content">

            <form @submit.prevent="contactFormSubmit()">

                <h2 class="heading mt-0">Get in touch</h2>

                <div class="flex-wrapper"> 

                    <div class="flex-item fb-xl-90 fb-xxl-70">

                        <div class="flex-wrapper form mt-1 pr-1">
                            
                            <div class="flex-item fb-sm-50 pl-1">

                                <div class="field isRequired">
                                    <div class="control pt-1">
                                        <input type="text" :class="{ 'isDanger' : !mail.message.fromName.isValid }" v-model="mail.message.fromName.value" placeholder="Name" />
                                        <span class="notification isDanger" v-html="mail.message.fromName.notification"></span>
                                    </div>
                                </div>

                                <div class="field isRequired">
                                    <div class="control pt-1">
                                        <input type="email" :class="{ 'isDanger' : !mail.message.fromEmail.isValid }" v-model="mail.message.fromEmail.value" placeholder="Email" />
                                        <span class="notification isDanger" v-html="mail.message.fromEmail.notification"></span>
                                    </div>
                                </div>

                                <div class="field">
                                    <div class="control pt-1">
                                        <input type="tel" :class="{ 'isDanger' : !mail.message.phone.isValid }" v-model="mail.message.phone.value" placeholder="Phone" />
                                        <span class="notification isDanger" v-html="mail.message.phone.notification"></span>
                                    </div>
                                </div>

                            </div>

                            <div class="flex-item fb-sm-50 pl-1">

                                <div class="field isRequired">
                                    <div class="control pt-1">
                                        <textarea :class="{ 'isDanger' : !mail.message.text.isValid }" v-model="mail.message.text.value" placeholder="Message"></textarea>
                                        <span class="notification isDanger" v-html="mail.message.text.notification"></span>
                                    </div>
                                </div>

                            </div>

                            <div class="flex-item pl-1"> 
                                <div class="field">
                                    <div class="control ptb-1">                       
                                        <button type="submit">Send</button>
                                    </div>
                                </div>
                            </div>

                            <div class="flex-item p1-1">
                                <span class="notification" v-html="mail.notification"></span>
                            </div>

                        </div>

                    </div>

                </div>     

                <vue-recaptcha ref="invisibleRecaptcha"
                    @verify="onRecaptchaVerify"
                    @expired="onRecaptchaExpired"
                    size="invisible"
                    sitekey="6LehCeUUAAAAAOzKRO1VqRLgYQEjOGK2Y6gR_YPc"
                    :loadRecaptchaScript="true">
                </vue-recaptcha>           

            </form>

        </section>
        
    </div>
</template>

<script>
import elasticemail from 'elasticemail-webapiclient';
import VueRecaptcha from 'vue-recaptcha';

export default {
    name: 'Contact',
    components: { VueRecaptcha },
    data() {
        return {
            formIsValid: true,
            mail: {                
                options: {
                    apiKey: 'C7DAC08514BC176826913DA29764F86BCF44E5317EE8CBE061253A814DAB6959E6D5B610E6F5E05513F4AD19E6679463',
                    apiUri: 'https://api.elasticemail.com/',
                    apiVersion: 'v2'
                },
                message: {
                    fromName: {
                        value: '',
                        isValid: true,
                        notification: ''
                    },
                    fromEmail: {
                        value: '',
                        isValid: true,
                        notification: ''
                    },
                    phone: {
                        value: '',
                        isValid: true,
                        notification: ''
                    },
                    text: {
                        value: '',
                        isValid: true,
                        notification: ''
                    },
                },
                notification: ''
            }
        }
    },
    methods: {
        contactFormSubmit() {

            let self = this;

            if(self.contactFormValidate()) {

                self.$refs.invisibleRecaptcha.execute();

            }            

        },
        contactFormValidate() {

            let self = this;

            self.formIsValid = true;

            self.mail.message.fromName.notification = '';
            self.mail.message.fromName.isValid = true;
            if(!self.mail.message.fromName.value.length) {
                self.mail.message.fromName.isValid = false;
                self.mail.message.fromName.notification = `Please enter your name.`;
                self.formIsValid = false;
            }

            self.mail.message.fromEmail.notification = '';
            self.mail.message.fromEmail.isValid = true;
            if(!self.mail.message.fromEmail.value.length || !self.validateEmail(self.mail.message.fromEmail.value)) {
                self.mail.message.fromEmail.isValid = false;
                self.mail.message.fromEmail.notification = `Please enter a valid email address.`;
                self.formIsValid = false;
            }

            self.mail.message.phone.notification = '';
            self.mail.message.phone.isValid = true;
            if(self.mail.message.phone.value.length && !self.validatePhone(self.mail.message.phone.value)) {
                self.mail.message.phone.isValid = false;
                self.mail.message.phone.notification = `Please enter a valid phone number.`;
                self.formIsValid = false;
            }

            self.mail.message.text.notification = '';
            self.mail.message.text.isValid = true;
            if(!self.mail.message.text.value.length) {
                self.mail.message.text.isValid = false;
                self.mail.message.text.notification = `Please enter a message.`;
                self.formIsValid = false;
            }

            return self.formIsValid;

        },
        onRecaptchaVerify(/*response*/) {

            let self = this;

            const bodyText = `Name: ${self.mail.message.fromName.value} \r\n` +
            `Email: ${self.mail.message.fromEmail.value} \r\n` +
            `Phone: ${self.mail.message.phone.value} \r\n` +
            `Message: ${self.mail.message.text.value} \r\n\r\n`;

            const ee = new elasticemail(self.mail.options);

            const emailParams = {
                "subject": 'New Enquiry',
                "to": 'enquiries@mikemooredev.com',
                "from": 'noreply@mikemooredev.com',                
                "fromName": self.mail.message.fromName.value,
                "sender": self.mail.message.fromEmail.value,
                "senderName": self.mail.message.fromName.value,
                "replyTo": self.mail.message.fromEmail.value,
                "replyToName": self.mail.message.fromName.value,
                "bodyText": bodyText,                
                "bodyType": 'Plain',
                "isTransactional": true
            };
            
            ee.Email.Send(emailParams)
            .catch((err) => {
                self.mail.notification = `<p class="isDanger boxed">${err}</p>`;
            })
            .then((result) => {

                if(result.success){
                    self.mail.notification = `<p class="isSuccess boxed">Thank you for getting in touch, I'll get back to you as soon as possible.</p>`;  
                }else{
                    self.mail.notification = `<p class="isDanger boxed">Oops, it looks like something went wrong, please email me directly at <a href="mailto:${emailParams.to}" title="Direct email: ${emailParams.to}">${emailParams.to}</a></p>`;
                }

            });

        },
        onRecaptchaExpired() {
            this.mail.notification = `<p class="isDanger">There has been an error processing your enquiry, please refresh the page and try again.</p>`;
        },
        validateEmail(email) {
            const re = /\S+@\S+\.\S+/;
            return re.test(email);
        },
        validatePhone(phone) {
            const re = /^[+]*[(]{0,1}[0-9]{1,3}[)]{0,1}[-\s./0-9]*$/;
            return re.test(phone);
        }
        
    }
}
</script>

<style lang="scss" scoped>
.form {
    background-color:color(grey-dark);
}
</style>