<template>
    <div class="blog blog-article content">

        <div class="flex-wrapper mrl-0-1">

            <div class="flex-item fb-md-75 prl-1">
                
                <ul class="breadcrumb">
                    <li><router-link to="/">Home</router-link></li>
                    <li><router-link to="/blog">Blog</router-link></li>
                    <li>{{ getArticle.title }}</li>
                </ul>

                <article class="content-article d-block w-100">

                    <component :is="getArticleContent"></component>

                    <p><sub class="grey" v-html="`Published: ${getArticle.date}`"></sub></p>

                </article>

                <div class="comments d-block w-100">
                    <p class="grey"><sub>Leave a comment, ask a question or just say hello.</sub></p>
                    <div id="disqus_thread"></div>
                </div>

            </div>

            <div class="flex-item fb-md-25 prl-1">

                <TagCloud :tags="getArticleTags" />

            </div>

        </div>

    </div>
</template>

<script>
import blogArticles from '@/components/content/blog/index.json'
import TagCloud from '@/components/blog/TagCloud'

export default {
    name: 'BlogArticle',
    props: {
        slug: {
            type: String,
            required: true
        }
    },
    components: {
        TagCloud
    },
    data() {
        return {
            pageTitle: ``,
            pageDescription: ``,
            pageUrl: `${window.location.href.replace(/\/$/, "")}/`,
            pageImage: ``,
            disqus_config: {}
        }
    },
    watch: {
        $route (){    
            this.setMeta()
        }
    },
    beforeMount() {
        this.setMeta()
        this.loadDisqus()    
    },
    computed: {
        getArticle() {
            const slug = this.slug;
            return blogArticles[slug];
        },
        getArticleTags() {
            const slug = this.slug;
            const article = blogArticles[slug];
            let tags = {}

            article.tags.forEach((item) => {
                tags[item] = [];
            });

            return tags;
        },
        getArticleContent() {
            const slug = this.slug;
            const filename = blogArticles[slug].filename;
            const markDownFile = require(`@/components/content/blog/articles/${filename}.md`);
            return markDownFile.default;
        }
    },
    methods: {
        loadDisqus() {
            let self = this;
            const disqus_config = function () {
                this.page.url = `${process.env.VUE_APP_URL}/blog/${self.slug}`;
                this.page.identifier = self.slug;
            };
            self.disqus_config = disqus_config;
            (function() {
                const d = document, s = d.createElement('script');
                s.src = 'https://mikemooredev.disqus.com/embed.js';
                s.setAttribute('data-timestamp', +new Date());
                (d.head || d.body).appendChild(s);
            })();
        },
        setMeta() {
            this.pageUrl = `${window.location.href.replace(/\/$/, "")}/`
            this.pageTitle = `${this.$route.meta.title} / ${this.getArticle.title}`
            this.pageDescription = `${this.getArticle.description}`
            this.pageImage = `${process.env.VUE_APP_URL}${this.getArticle.thumbnail}`
            this.$parent.updateTag(`title`,``,``,``,this.pageTitle)
            this.$parent.updateTag(`link`,`rel`,`canonical`,`href`,this.pageUrl)
            this.$parent.updateTag(`meta`,`name`,`title`,`content`,this.pageTitle)
            this.$parent.updateTag(`meta`,`name`,`description`,`content`,this.pageDescription)
            this.$parent.updateTag(`meta`,`name`,`twitter:title`,`content`,this.pageTitle)
            this.$parent.updateTag(`meta`,`name`,`twitter:description`,`content`,this.pageTitle)
            this.$parent.updateTag(`meta`,`name`,`twitter:image`,`content`,this.pageImage)
            this.$parent.updateTag(`meta`,`name`,`twitter:image:alt`,`content`,this.getArticle.thumbnail_alt)
            this.$parent.updateTag(`meta`,`property`,`og:type`,`content`,`article`)
            this.$parent.updateTag(`meta`,`property`,`og:title`,`content`,this.pageTitle)
            this.$parent.updateTag(`meta`,`property`,`og:description`,`content`,this.pageDescription)
            this.$parent.updateTag(`meta`,`property`,`og:url`,`content`,this.pageUrl)
            this.$parent.updateTag(`meta`,`property`,`og:image`,`content`,this.pageImage)
        }
    }
}
</script>